import React from 'react';
import { TaskStateChoice } from './TaskState/TaskStateChoice';
import { Field } from '../Utility/Field/Field';
import { newState } from '../../AppState/Tasks/service';
import { Task } from '../../AppState/Tasks/model';

export type TaskFormProps = {
  task: Task;
  onChange: (task: Task) => void;
};

export const TaskForm: React.FC<TaskFormProps> = ({ task, onChange }) => {
  return (
    <>
      <TaskStateChoice state={task.state} onChange={(state) => onChange({ ...task, ...newState(state) })} />
      <Field label="titre">
        <input type="text" value={task.title} onChange={(event) => onChange({ ...task, title: event.target.value })} />
      </Field>
      <Field label="description">
        <textarea
          value={task.description}
          onChange={(event) => onChange({ ...task, description: event.target.value })}
        />
      </Field>
    </>
  );
};
