import React from 'react';
import styles from './Tasks.module.scss';
import { TaskComponent } from './TaskComponent';
import { TaskEditionDialog } from './TaskEditionDialog';
import { useTasks } from '../../AppState/Tasks/hooks';
import { TaskModel, Task, TaskState } from '../../AppState/Tasks/model';
import { Button } from '../Utility/Button/Button';
import { PageLayout } from '../App/PageLayout';
import { FilterImage } from '../../Assets/FilterImage';
import { TaskFilter } from './TaskFilter';

function getStateOrder(state: TaskState): number {
  switch (state) {
    case 'active':
      return 0;
    case 'in-backlog':
      return 1;
    case 'created':
      return 2;
    case 'finished':
      return 3;
    case 'abandoned':
      return 4;
  }
}

function compare(task1: Task, task2: Task): number {
  if (task1.state !== task2.state) {
    return getStateOrder(task1.state) < getStateOrder(task2.state) ? -1 : 1;
  }

  // both tasks have the same state

  return task1.title.localeCompare(task2.title);
}

function toComparable(text: string): string {
  return text
    .toLowerCase()
    .normalize('NFD')
    .replace(/[\u0300-\u036f]/g, '');
}

function sortAndFilterTasks(tasks: TaskModel, text: string): Task[] {
  var result = Object.keys(tasks)
    .map((id) => tasks[id])
    .sort(compare);

  if (text) {
    text = toComparable(text);
    result = result.filter(
      (task) => toComparable(task.title).includes(text) || toComparable(task.description).includes(text)
    );
  }

  return result;
}

/** page with the list of tasks, a filter, a "new task" button and an edition dialog, if needed. */
export function TaskList() {
  var tasks = useTasks();
  const [filterVisible, setFilterVisible] = React.useState<boolean>(false);
  const [filterText, setFilterText] = React.useState<string>('');
  const [newTaskFormOpen, setNewTaskFormOpen] = React.useState(false);

  /*React.useEffect(() => {
    (async () => {
      try {
        setData(await getData());
      } catch (error) {
        setData(error.toString());
      }
    })();
  }, []);*/

  return (
    <PageLayout
      headerControls={
        <Button onClick={() => setFilterVisible(!filterVisible)} isLight={true} title="Filtrer">
          <FilterImage height={20} width={20} stroke="white" />
        </Button>
      }
    >
      <div className={styles.taskList}>
        <TaskFilter visible={filterVisible} text={filterText} textChanged={setFilterText} />
        <div className={styles.tasks}>
          {sortAndFilterTasks(tasks, filterVisible ? filterText : '').map((task) => (
            <TaskComponent key={task.id} task={task} />
          ))}
        </div>
        <div className={styles.tasksButtons}>
          <Button isWide onClick={() => setNewTaskFormOpen(true)}>
            +
          </Button>
        </div>
        {newTaskFormOpen && <TaskEditionDialog task={undefined} onClose={() => setNewTaskFormOpen(false)} />}
      </div>
    </PageLayout>
  );
}
