import React from 'react';
import styles from './Tasks.module.scss';
import { getClasses } from '../../Helpers/cssHelper';

type TaskFilterProps = {
  visible: boolean;
  text: string;
  textChanged: (text: string) => void;
};

export const TaskFilter: React.FunctionComponent<TaskFilterProps> = ({ visible, text, textChanged }) => {
  var textRef = React.useRef<HTMLInputElement | null>(null);

  React.useEffect(() => {
    if (visible && textRef.current) {
      textRef.current.focus();
    }
  }, [visible]);

  return (
    <div className={getClasses(styles.tasksFilter, visible && styles.visible)}>
      <div className={styles.tasksFilterInner}>
        <input
          ref={textRef}
          className={styles.taskFilterText}
          value={text}
          onChange={(ev) => textChanged(ev.target.value)}
        />
      </div>
    </div>
  );
};
