import React from 'react';
import { TaskModel, SaveStatus } from './model';
import { ITaskService, TaskService, ITaskStore } from './service';
import { AuthManager } from '../../Auth/AuthManager';
import { DataStore } from '../../DataStore/DataStore';
import { ParticleState } from '../../ParticleState/ParticleState';
import { IDataStore } from '../../DataStore/IDataStore';

const TasksContext = React.createContext<TaskModel>(undefined as any);
const TaskServiceContext = React.createContext<ITaskService>(undefined as any);
const SaveStatusContext = React.createContext<SaveStatus>(undefined as any);

type AutoSaverState = {
  // the model being saved. Undefined if no model needs to be saved.
  modelBeingSaved?: TaskModel;
  // A model queued to be saved, if a save operation is already ongoing (modelBeingSaved is thus not undefined), undefined otherwise.
  modelToSave?: TaskModel;
};

const useAutoSaver = (dataStore: IDataStore, taskStore: ITaskStore): SaveStatus => {
  const state = React.useRef<AutoSaverState>({});
  const [saveStatus, setSaveStatus] = React.useState<SaveStatus>('all-saved');

  const onModelChange = React.useCallback(
    (model: TaskModel) => {
      if (state.current.modelBeingSaved) {
        // a save operation is already ongoing. Queue the model to be saved, overwriting an older version already queued (if any).
        state.current.modelToSave = model;
        return;
      }

      (async () => {
        state.current = { modelBeingSaved: model };
        setSaveStatus('saving');
        await dataStore.saveData(model);

        while (state.current.modelToSave) {
          state.current = { modelBeingSaved: model };
          await dataStore.saveData(model);
        }

        state.current = {};
        setSaveStatus('all-saved');
      })();
    },
    [dataStore]
  );

  React.useEffect(() => taskStore.subscribe(onModelChange), [taskStore, onModelChange]);
  return saveStatus;
};

interface ITasksProps {
  dataStore: IDataStore;
  taskStore: ITaskStore;
}

const Tasks: React.FC<ITasksProps> = ({ dataStore, taskStore, children }) => {
  const [tasks, setTasks] = React.useState<TaskModel>(taskStore.getState());
  const taskService = React.useMemo(() => new TaskService(taskStore), [taskStore]);
  const saveStatus = useAutoSaver(dataStore, taskStore);

  React.useEffect(() => taskStore.subscribe(setTasks), [taskStore]);

  return (
    <TasksContext.Provider value={tasks}>
      <TaskServiceContext.Provider value={taskService}>
        <SaveStatusContext.Provider value={saveStatus}>{children}</SaveStatusContext.Provider>
      </TaskServiceContext.Provider>
    </TasksContext.Provider>
  );
};

/** Maintains the task state and the data store. Ensures authentication. */
export const TasksProvider: React.FC = ({ children }) => {
  const authManager = React.useMemo(() => new AuthManager(), []);
  const dataStore = React.useMemo(() => new DataStore(authManager), [authManager]);
  const [taskStore, setTaskStore] = React.useState<ITaskStore | undefined>(undefined);

  React.useEffect(() => {
    (async () => {
      const data = await dataStore.loadData();
      const store = new ParticleState<TaskModel>(data);
      setTaskStore(store);
    })();
  }, [dataStore]);

  if (!taskStore) {
    return null;
  }

  return (
    <Tasks dataStore={dataStore} taskStore={taskStore}>
      {children}
    </Tasks>
  );
};

export const useTasks = () => React.useContext(TasksContext);
export const useTaskService = () => React.useContext(TaskServiceContext);
export const useSaveStatus = () => React.useContext(SaveStatusContext);
