import { IDataStore } from './IDataStore';
import { IAuthManager } from '../Auth/IAuthManager';
import { TaskModel, Task } from '../AppState/Tasks/model';
import { StoreTask, DataStoreModel } from './DataStoreModel';

export class DataStore implements IDataStore {
  readonly authManager: IAuthManager;

  public constructor(authManager: IAuthManager) {
    this.authManager = authManager;
  }

  async loadData() {
    var accessToken = await this.authManager.acquireToken();

    var response = await fetch(
      'https://graph.microsoft.com/v1.0/me/drive/root:/boussole-data-files/notes.json:/content',
      { headers: { Authorization: 'Bearer ' + accessToken } }
    );

    var storeModel = await response.json();
    return getAppState(storeModel);
  }

  async saveData(data: TaskModel) {
    var storeModel = getStoreModel(data);
    var accessToken = await this.authManager.acquireToken();

    await fetch('https://graph.microsoft.com/v1.0/me/drive/root:/boussole-data-files/notes.json:/content', {
      method: 'PUT',
      headers: {
        'Content-Type': 'text/plain',
        Authorization: 'Bearer ' + accessToken,
      },
      body: JSON.stringify(storeModel, null, '  '),
    });
  }
}

// *** Conversion from Appliation Model to Data Store Model and vice versa ***

function getStoreTask(task: Task): StoreTask {
  return {
    state: task.state === 'created' ? undefined : task.state,
    id: task.id,
    title: task.title,
    description: task.description || undefined,
    created: task.created,
    modified: task.modified,
    dateClosed: task.dateClosed,
  };
}

function getStoreModel(appState: TaskModel): DataStoreModel {
  const tasks: StoreTask[] = [];
  for (const taskId in appState) {
    tasks.push(getStoreTask(appState[taskId]));
  }

  return { tasks };
}

function getTask(storeTask: StoreTask): Task {
  return {
    state: storeTask.state || 'created',
    id: storeTask.id,
    title: storeTask.title,
    description: storeTask.description || '',
    created: storeTask.created,
    modified: storeTask.modified,
    dateClosed: storeTask.dateClosed,
  };
}

function getAppState(storeModel: DataStoreModel): TaskModel {
  const tasks: TaskModel = {};

  storeModel.tasks.forEach((storeTask) => (tasks[storeTask.id] = getTask(storeTask)));

  return tasks;
}
