import React from 'react';
import styles from './App.module.scss';
import { Link } from 'react-router-dom';
import { useSaveStatus } from '../../AppState/Tasks/hooks';
import { SaveImage } from '../../Assets/SaveImage';
import { getClasses } from '../../Helpers/cssHelper';

type PageLayoutProps = {
  /** optional header controls to be rendered */
  headerControls?: React.ReactElement;
};

/** renders a header with various buttons and the children as the content */
export const PageLayout: React.FunctionComponent<PageLayoutProps> = ({ headerControls, children }) => {
  var saveStatus = useSaveStatus();

  return (
    <>
      <header>
        <Link to="/home">Home</Link>
        <Link to="/tasks">Tâches</Link>
        <span
          title={saveStatus === 'saving' ? "En cours d'enregistrement..." : 'Toutes les données sont sauvegardées.'}
        >
          <SaveImage
            className={getClasses(styles.saveButton, saveStatus === 'saving' && styles.saving)}
            height={20}
            width={20}
          />
        </span>
        {headerControls}
      </header>
      <section>{children}</section>
    </>
  );
};
