import { newGuid } from '../../Helpers/guidHelper';
import { IParticleState } from '../../ParticleState/IParticleState';
import { Task, TaskState, TaskModel } from './model';

export const getNewTask = (): Task => ({
  id: '',
  state: 'created',
  title: '',
  description: '',
  created: new Date(),
  modified: new Date(),
});

export const newState = (state: TaskState): Partial<Task> => {
  return {
    state,
    dateClosed: state === 'finished' || state === 'abandoned' ? new Date() : undefined,
  };
};

export type ITaskStore = IParticleState<TaskModel>;

export interface ITaskService {
  createTask: (task: Task) => void;
  updateTask: (id: string, task: Partial<Task>) => void;
  getTasks(): TaskModel;
  setTasks(tasks: TaskModel): void;
}

export class TaskService implements ITaskService {
  private store: ITaskStore;

  constructor(store: ITaskStore) {
    this.store = store;
  }

  public getTasks = () => this.store.getState();

  public setTasks = (tasks: TaskModel) => this.store.setState(tasks);

  public createTask = (task: Task) => {
    this.store.updateState((tasks) => {
      const id = newGuid();
      const now = new Date();
      tasks[id] = {
        ...task,
        id,
        created: now,
        modified: now,
      };
      return { ...tasks };
    });
  };

  public updateTask = (id: string, updates: Partial<Task>) => {
    this.store.updateState((tasks) => {
      var task = tasks[id];
      if (!task) throw new Error(`No task found with id {id}`);
      tasks[id] = {
        ...task,
        ...updates,
      };
      return { ...tasks };
    });
  };
}
